[name*="about-me"] {
  margin-bottom: 6rem;
}

.about-me_grid__2F-dQ {
  padding: 0 1rem;
  font-size: 1.5rem;
  text-align: center;
}

.about-me_grid__2F-dQ h1 {
  grid-area: h1;
}

.about-me_grid__2F-dQ main {
  grid-area: main;
}

.about-me_grid__2F-dQ aside {
  grid-area: aside;
}

.about-me_grid__2F-dQ > a {
  grid-area: a;
}

.about-me_grid__2F-dQ main a {
  display: none;
}

.about-me_grid__2F-dQ img {
  width: 260px;
  height: auto;
  shape-outside: circle();
  border-radius: 50%;
  margin: 1em 0 2rem;
}

.about-me_grid__2F-dQ aside {
  font-family: "Playfair+Display", serif;
  font-weight: 400;
  font-size: 2rem;
  line-height: 1.15;
  color: #555555;
  margin: 2rem 1rem;
}

.about-me_grid__2F-dQ span {
  color: #dc5f51;
}

.about-me_grid__2F-dQ a {
  font-style: italic;
  color: #dc5f51;
}

.about-me_grid__2F-dQ a:hover {
  color: #555555;
}

/*
--breakpoint-xs: 0;
--breakpoint-sm: 576px;
--breakpoint-md: 768px;
--breakpoint-lg: 992px;
--breakpoint-xl: 1200px;
*/

@media (min-width: 480px) {
  .about-me_grid__2F-dQ aside {
    margin: 2rem 6rem;
  }
}

@media (min-width: 768px) {
  .about-me_grid__2F-dQ {
    padding: 0 2rem;
    height: 100vh;
    text-align: unset;

    display: grid;
    grid-template-rows: auto 60px auto auto auto;
    grid-template-areas:
      "h1"
      "."
      "main"
      "aside"
      "a";
  }

  .about-me_grid__2F-dQ p:first-of-type {
    margin-top: -60px;
  }

  .about-me_grid__2F-dQ img {
    float: right;
    margin: 0.2em;
  }

  .about-me_grid__2F-dQ aside {
    width: 60%;
    margin: 0;
  }
}

@media (min-width: 1200px) {
  .about-me_grid__2F-dQ {
    max-width: 1140px;
    height: 100vh;
    margin-right: auto;
    margin-left: auto;
    padding: 0;
    grid-column-gap: 2vw;
    grid-template-columns: 1fr 5fr 2fr 1fr;
    grid-template-rows: 100px auto auto;
    grid-template-areas:
      ". . . ."
      "h1 main . ."
      "h1 main aside .";
  }

  .about-me_grid__2F-dQ h1 {
    -webkit-writing-mode: vertical-rl;
            writing-mode: vertical-rl;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    align-self: flex-start;
    justify-self: flex-end;
    margin: 0;
  }

  .about-me_grid__2F-dQ main {
    font-size: 1.6rem;
    line-height: 1.3;
  }

  .about-me_grid__2F-dQ p:first-of-type {
    margin-top: 0;
  }

  .about-me_grid__2F-dQ aside {
    -webkit-transform: rotate(7deg);
            transform: rotate(7deg);
    width: auto;
    margin: 0;
    font-size: 1.75rem;
    line-height: 1;
    color: #777;
  }

  .about-me_grid__2F-dQ main a {
    display: inline;
  }

  .about-me_grid__2F-dQ > a {
    display: none;
  }
}

